import { Component, OnInit ,ViewChild,ElementRef} from '@angular/core';
import { ApiService } from '../../../shared/services/api.service';
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import { UserService } from '../../../shared/services/user.service';
import {  MatDialog } from '@angular/material';
import { DialogModalComponent } from './dialog-modal/dialog-modal.component';
import { environment } from "../../../../environments/environment";

@Component({
  selector: 'app-csv',
  templateUrl: './csv.component.html',
  styleUrls: ['./csv.component.scss']
})
export class CsvComponent implements OnInit {
  form: FormGroup;
  loading: boolean = false;
  filesToUpload: Array<File>;
  disbleButton :boolean = false;
  SERVER_URL = environment.SERVER_URL || 'http://localhost:3100'

  @ViewChild('fileInput') fileInput: ElementRef;
  constructor(
    private apiService :ApiService,
    private fb: FormBuilder,
    private userService : UserService,
    public dialog: MatDialog
  ) {
    this.createForm();
    this.filesToUpload = [];
   }

   createForm() {
    this.form = this.fb.group({
      name: ['', Validators.required],
      avatar: null
    });
  }

  ngOnInit() {
  }

  onFileChange(evt: any) {
    this.disbleButton =  true;
    this.filesToUpload = <Array<File>> evt.target.files;
  }

  upload() {
    this.loading = true;
    this.disbleButton =  false;
    this.makeFileRequest(this.SERVER_URL+"/api/csv/uploadCsv", [], this.filesToUpload).then((result) => {
        this.loading = false;
        this.disbleButton =  true;
           if(!result['success']){;
         
        }else{
          this.clearFile()
        }
         this.dialog.open(DialogModalComponent, {
          data: result
        });
     
    }, (error) => {
        console.error('errrr',error);
    });
}



  makeFileRequest(url: string, params: Array<string>, files: Array<File>) {
    return new Promise((resolve, reject) => {
        var formData: any = new FormData();
        var xhr = new XMLHttpRequest();
        var token ={
          token : this.userService.user.token
        }
        for(var i = 0; i < files.length; i++) {
            formData.append("data", files[i], files[i].name);

        }
       
        xhr.onreadystatechange = function () {
            if (xhr.readyState == 4) {
                if (xhr.status == 200) {
                    resolve(JSON.parse(xhr.response));
                } else {
                    reject(xhr.response);
                }
            }
        }
        xhr.open("POST", url, true);
        xhr.setRequestHeader('x-access-token', this.userService.user.token);
        xhr.send(formData);
    });
}

  clearFile() {
    this.disbleButton =  false;
    this.form.get('avatar').setValue(null);
    this.fileInput.nativeElement.value = '';
  }

}



