import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { IntegrationsRoutingModule } from './integrations-routing.module';
import { IntegrationsComponent } from './integrations/integrations.component';
import { IntegrationsPageComponent } from './integrations-page/integrations-page.component';
import { SharedModule } from '../../shared/shared.module';
import { CsvComponent } from './csv/csv.component';
import { DialogModalComponent } from './csv/dialog-modal/dialog-modal.component';
import { NeedlesSetupComponent } from './needles-setup/needles-setup.component';

@NgModule({
  imports: [
    CommonModule,
    IntegrationsRoutingModule,
      SharedModule
  ],
  declarations: [IntegrationsComponent, IntegrationsPageComponent, NeedlesSetupComponent, CsvComponent,DialogModalComponent],
  entryComponents: [
   DialogModalComponent
  ]
})
export class IntegrationsModule { }



