import { Component, OnInit } from '@angular/core';
import { UserService } from '../../../shared/services/user.service';
import { GeneralService } from '../../../shared/services/general.service';
import { ApiService } from '../../../shared/services/api.service';

@Component({
  selector: 'app-needles-setup',
  templateUrl: './needles-setup.component.html',
  styleUrls: ['./needles-setup.component.scss']
})
export class NeedlesSetupComponent implements OnInit {
    savingSetp3Data = false;
    ip;
    port;
    sync_interval;

    constructor(
        private apiService: ApiService,
        private userService: UserService,
        private generalService: GeneralService,
    ) { }

    ngOnInit() {

        this.loadSettings();

    }

    loadSettings(){
        this.apiService.call('/api/integrations/needles/get', {account: this.userService.user.accountId}).subscribe((result: any ) => {
            if(result.success==true){
                const setting = result.setting;
                if(result.setting){
                    this.ip = setting.ip;
                    this.port = setting.port;
                    this.sync_interval = setting.sync_interval;
                }
            }
        });
    }

    downloadZippedApplication(){
        window.open(this.apiService.SERVER_URL + '/api/download-zipped-integration');
    }

    saveStep3(){
        this.savingSetp3Data = true;
        this.apiService.call('/api/integrations/needles/save', {ip: this.ip, sync_interval: this.sync_interval, port: this.port, account: this.userService.user.accountId, }).subscribe((response: any) => {

            if(response.success == true){
                this.savingSetp3Data = false;

                this.generalService.showNotification('Settings are saved successfully.', 'Okay');

            }

        }, ()=>{
            this.savingSetp3Data = false;
        });
    }

}



