import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { IntegrationsComponent } from './integrations/integrations.component';
import { IntegrationsPageComponent } from './integrations-page/integrations-page.component';
import { NeedlesSetupComponent } from './needles-setup/needles-setup.component';
import { CsvComponent } from './csv/csv.component';


const routes: Routes = [
    {
        path: '', children: [
            { path: 'needles/setup', component: NeedlesSetupComponent },
            { path: '', component: IntegrationsComponent },
            { path: 'CSV/Instructions', component: CsvComponent }
        ], component: IntegrationsPageComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class IntegrationsRoutingModule { }


